import Vue from 'vue';
import {Component, Prop, Watch} from "vue-property-decorator";
import Lesson, {Marks} from "@/api/models/Lesson";

@Component({})
export default class Grading extends Vue {
    public markEditing!: boolean;
    public marks: any;

    @Prop({type: Object as () => Lesson})
    public lesson!: Lesson;

    @Prop({
        default: () => false
    })
    public editing!: boolean;

    constructor() {
        super();
        this.markEditing = this.editing;
        this.marks = [
            {value: Marks.None, text: ""},
            {value: Marks.Excellent, text: " (5)"},
            {value: Marks.Good, text: " (4)"},
            {value: Marks.Satisfactory, text: ". (3)"},
            {value: Marks.Unsatisfactory, text: ". (2)"},
            {value: Marks.Poor, text: " (1)"},
        ]
    }

    @Watch('editing')
    updateMarkEditing() {
        this.markEditing = this.editing
    }

    get markName(): string {
        return this.marks.filter((m: any) => m.value == this.lesson.selfMark)[0].text;
    }

    setMark() {
        this.markEditing = !this.markEditing;
    }
}